


//logo
.hide-text {
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
}

//btn 



.btn-primary{
  color: #fff;
  background-color: $color-primary;
}

.btn-secondary{
  color: #fff;
  background-color: $color-secondary;
}
// block
.d-block{
  display: block;
}

// flex
.d-flex {
  display: flex;
}



.d-none{
  display: none;
}

.flex-column {
  flex-direction: column;
}

.flex-row-reverse {
  flex-direction: row-reverse;
}
.flex-row {
  flex-direction: row;
}

.flex-wrap{
  flex-wrap: wrap;
}

.justify-content-between {
  justify-content: space-between;
}
.justify-content-end {
  justify-content: flex-end;
}
.justify-content-center {
  justify-content: center;
}
.align-items-center {
  align-items: center;
}
.align-items-end {
  align-items: flex-end;
}
.flex-center{
  justify-content: center;
  align-items: center;
}


// bg

.bg-cover-center {
  background-position: center center;
  background-size: cover;
}

.bg-transparent{
  background-color: transparent;
}
.bg-primary {
  background-color: $color-primary;
}
.bg-secondary {
  background-color: $color-secondary;
}
.bg-third {
  background-color: $color-third;
}

//text
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-left {
  text-align: left;
}


.text-white {
  color: $text-white;
}
.text-primary{
  color: $color-primary;  
}
.text-secondary{
  color: $color-secondary;  
}
.text-muted{
  color: $color-lighter-1;
}
.border-bottom{
  border-bottom: 1px solid;
}
.border-white{
  border-color: $white;
}



// position 
.position-relative{
  position: relative;
}

.position-absolute{
  position: absolute;
}

.positionABCenter{
  @include positionABCenter;
}

@include pad{
  .positionABCenter-lg{
    @include positionABCenter;
  }
}

@include mobile{
  .positionABCenter-md{
    @include positionABCenter;
  }
}



@include addGrid('.d', 'block', 'display', 'block') {}
@include addGrid('.d', 'flex', 'display', 'flex') {}
@include addGrid('.d', 'none', 'display', 'none') {}
@include addGrid('.flex', 'column', 'flex-direction', 'column') {}
@include addGrid('.flex', 'row', 'flex-direction', 'row') {}
@include addGrid('.flex-row', 'reverse', 'flex-direction', 'row-reverse') {}
@include addGrid('.justify-content', 'between', 'justify-content', 'space-between') {}
@include addGrid('.position', 'relative', 'position', 'relative') {}
@include addGrid('.position', 'absolute', 'position', 'absolute') {}
